.container {
  width: 600px;
  height: 700px;
  margin: auto;
  margin-top: 40px;
  border-radius: 40px;
  background-image: linear-gradient(to bottom, #f76700, #0eceb8);
}

.topBar {
  display: flex;
  justify-content: center;
  gap: 14px;
  padding-top: 50px;
}

.topBar input {
  display: flex;
  width: 362px;
  height: 60px;
  background: #ebfffc;
  border: none;
  outline: none;
  border-radius: 40px;
  padding-left: 40px;
  color: #626262;
  font-size: 20px;
  font-weight: 500;
}

.searchIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  background: #ebfffc;
  border-radius: 40px;
  cursor: pointer;
}

.weatherImage {
  margin-top: 29px;
  display: flex;
  justify-content: center;
}

.weatherTemp {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 100px;
  font-weight: 500;
}

.weatherLocation {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 40px;
  font-weight: 500;
}

.dataContainer {
  margin-top: 50px;
  color: white;
  display: flex;
  justify-content: center;
}

.element {
  margin: auto;
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 20px;
}

.data {
  font-size: 34px;
  font-weight: 500;
}

.text {
  font-size: 20px;
  font-weight: 500;
}

.icon {
  margin-top: 10px;
}
